import React from 'react'
import { render } from 'react-dom'
import '../../App.css';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import Button from 'react-bootstrap/Button';
//import touchAble from './Content.json'
//import scrollscript from './scrollscript'
import data from './Navibar.json';



export default class Navibar extends React.Component{

  constructor(props) {
    super(props);
    this.state = {page_id: this.props.page_id,  data: data};
    //var super_header = React.createElement('navclass',{id: 'navigation'})
  }
//<a class="nav-link"  href="#" onClick={app_id = {product.app_id}, this.props.selectProductPage.bind(this,app_id)}>{product.app_id}</a>

render() {
    let app_id = this.state.page_id
    let content;
    var nav_list;
    var products = this.state.data.products

      nav_list = products.map(product => {
        if (app_id === product.app_id) {
        return (
        <div>
        <li class="nav-item active">
            <a class="nav-link "  href="#" onClick={this.props.selectProductPage.bind(this,product.app_id)}>{product.app_id}</a>
        </li>
        </div>
        )
        }
    else {
    return (
        <div>
        <li class="nav-item">
            <a class="nav-link "  href="#" onClick={this.props.selectProductPage.bind(this,product.app_id)}>{product.app_id}</a>
        </li>
        </div>
    )
    }
})

return (

 <nav class="navbar navbar-expand-md navbar-dark bg-dark  bg-faded justify-content-center bottom_border_class">
             <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNavDropdown" aria-controls="navbarNavDropdown" aria-expanded="false" aria-label="Toggle navigation">
    <span class="navbar-toggler-icon"></span>
  </button>
    <ul class="navbar-nav w-100 justify-content-start">
    <li class="nav-item active">
     <a class="navbar-brand" href="#" onClick={app_id = "zerodebug", this.props.selectProductPage.bind(this,app_id)}><img src="./logo_zerodebug_white.png" width="30"></img></a> 
        </li>
        
    <li class="nav-item">
     <a class="navbar-brand" href="#" onClick={app_id = "zerodebug", this.props.selectProductPage.bind(this,app_id)}>zerodebug</a> 
        </li>
        {nav_list}
    </ul>
    <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#collapsingNavbar3">
        <span class="navbar-toggler-icon"></span>
    </button>
    <div class="navbar-collapse collapse w-100" id="collapsingNavbar3">
<ul class="nav navbar-nav ml-auto w-100 justify-content-center">
<li class="nav-item active">
     <a class="navbar-brand" href="#" onClick={this.props.selectProductPage.bind(this,app_id)}>{content}</a> 
        </li>
</ul>
</div>
    <div class="navbar-collapse collapse w-100" id="collapsingNavbar3">

        <ul class="nav navbar-nav ml-auto w-100 justify-content-end">
            <li class="nav-item">
                <a class="nav-link" href="#">Helpdesk</a>
            </li>
            <li class="nav-item">
                <a class="nav-link" href="#">Forum</a>
            </li>
        </ul>
    </div>
</nav> 

);
}
}
